import modelExtend from 'dva-model-extend'
import { queryWorksLists } from '../../services/works'
import { pageModel } from '../common'
import queryString from 'query-string'
import { IWstate } from './interface'

export default modelExtend(pageModel, {
  namespace: 'notpass',
  state: {
    currentItem: {},
    modalVisible: false,
  },
  subscriptions: {
    setup ({ dispatch, history }: any) {
      history.listen((location: any) => {
        if (location.pathname === '/works/notpass') {
          dispatch({
            type: 'query',
            payload: {
              // status: 2,
              ...queryString.parse(location.search),
            },
          })
        }
      })
    },
  },
  effects: {
    * query ({ payload }: any, { call, put, select }: any) {
      const searchValue = yield select((state: any) => state.notpass.searchValue)
      const data = yield call(queryWorksLists, { ...payload, status: 2, name: searchValue })
      if (data.success) {
        const { work } = data.data
        yield put({
          type: 'querySuccess',
          payload: {
            list: work.data,
            pagination: {
              current: Number(payload.current_page) || 1,
              pageSize: Number(payload.per_page) || 10,
              total: work.total,
            },
          },
        })
      } else {
        throw data
      }
    },
  },
  reducers: {
    updateState (state: IWstate, { payload }: any) {
      return {
        ...state,
        ...payload,
      }
    },
  },
})



