import config from '../utils/config'
import request from '../utils/request'
const { api } = config

const {
  worksLists, commentList, passWorks, delleteComment,
} = api

export function queryWorksLists (params: any) {
  return request({
    url: worksLists,
    method: 'get',
    data: params,
  })
}
export function queryPassWorks (params: any) {
  return request({
    url: passWorks.replace(':id', params.id),
    method: 'put',
    data: params,
  })
}
export function queryCommentList (params: any) {
  return request({
    url: commentList,
    method: 'get',
    data: params,
  })
}
export function delComment (params: any, userId: any) {
  return request({
    url: delleteComment.replace(':id', userId.user_id),
    method: 'delete',
    data: params,
  })
}
